form{
    width: 90%;
    max-width: 600px;
    background-color: #fff;
    padding: 33px, 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

label{
    text-align: center;
    font-weight: bold;
    font-size:20px;
    color: #1E1E1E;
    margin-bottom: 26px;
}

textarea{
    background-color: #f9fbfc;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32,35,40,0.25);
    color: #1E1E1f;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100%-25px);
}

textarea::placeholder{
    color: #a5a5a5;
    font-family: "Monserrat";
    font-weight: 400;
}

textarea:focus{
    outline-color: #61dafa;
}

.TodoForm-buttonContainer{
    margin-top: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}   

.TodoForm-button{
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: "Monserrat";
}

.TodoForm-button--add{
    background-color: #61dafa;
    box-shadow: 0px 5px 50px rgba(97,218,250,0.5);
}

.TodoForm-button--cancel{
    background-color: transparent;
}